const count_moves = (temp, color) => {
  let a = [
    [1, 2, 3, 4],
    [5, 6, 7, 8],
    [9, 1, 1, 1],
    [1, 1, 1, 1],
  ];

  for (let i = 0; i < 4; i++) {
    for (let j = 0; j < 4; j++) {
      a[i][j] = temp[i * 4 + j];
    }
  }
  //console.log("hellooo "+a);
  let ans=0;
    let my,opp;
    if(color=='red')
    {
        my=1;
        opp=2;
    }
    else
    {
        my=2;
        opp=1;
    }
    for(let i=0;i<4;i++)
    {
        for(let j=0;j<4;j++)
        {
            if(a[i][j]!=opp&&a[i][j]!=3)
            {
                if(i+1<4 && a[i+1][j]!=opp&&a[i+1][j]!=3)
                {
                    if(j+2<4 && a[i+1][j+1]!=3&&a[i+1][j+1]!=opp &&a[i+1][j+2]!=3&&a[i+1][j+2]!=opp)
                    ans++;
                    if(j-2>=0 && a[i+1][j-1]!=3&&a[i+1][j-1]!=opp &&a[i+1][j-2]!=3&&a[i+1][j-2]!=opp)
                    ans++;
                }
                if(i-1>=0 && a[i-1][j]!=opp&&a[i-1][j]!=3)
                {
                    if(j+2<4 && a[i-1][j+1]!=3&&a[i-1][j+1]!=opp &&a[i-1][j+2]!=3&&a[i-1][j+2]!=opp)
                    ans++;
                    if(j-2>=0 && a[i-1][j-1]!=3&&a[i-1][j-1]!=opp &&a[i-1][j-2]!=3&&a[i-1][j-2]!=opp)
                    ans++;
                }
                if(j+1<4&&a[i][j+1]!=opp&&a[i][j+1]!=3)
                {
                    if(i+2<4&&a[i+1][j+1]!=opp && a[i+1][j+1]!=3 && a[i+2][j+1]!=opp && a[i+2][j+1]!=3)
                    ans++;
                    if(i-2>=0&&a[i-1][j+1]!=opp && a[i-1][j+1]!=3 && a[i-2][j+1]!=opp && a[i-2][j+1]!=3)
                    ans++;
                }
                if(j-1>=0&&a[i][j-1]!=opp&&a[i][j-1]!=3)
                {
                    if(i+2<4&&a[i+1][j-1]!=opp && a[i+1][j-1]!=3 && a[i+2][j-1]!=opp && a[i+2][j-1]!=3)
                    ans++;
                    if(i-2>=0&&a[i-1][j-1]!=opp && a[i-1][j-1]!=3 && a[i-2][j-1]!=opp && a[i-2][j-1]!=3)
                    ans++;
                }
            }
        }
    }
    //console.log(a,ans);
    return ans-1;
};
export default count_moves;
