import React, { useState, useEffect } from "react";
import "./Tutorial.css";
import video1 from "../video/video-1.mp4";
import video2 from "../video/video-2.mp4";
function Tutorial() {
  let arr = [
    video1,
    video2,
    "Move your L-piece by selection cells on the grid.",
    "Move one of the neutral pieces or skip the step",
  ];
  const [state, setState] = useState(0);
  const [video, setVideo] = useState(arr[1]);
  const [content, setContent] = useState(arr[3]);

  useEffect(() => {
    if (video === arr[0]) {
      setVideo(arr[1]);
      setContent(arr[3]);
    } else {
      setVideo(arr[0]);
      setContent(arr[2]);
    }
    changeSource(video);
    //console.log(video + "  " + content);
  }, [state]);

  function changeSource(url) {
    var video = document.getElementById("video");
    video.src = url;
    video.play();
  }
  return (
    <div className="tutorial">
      <div className="tutorial-section">
        <div className="video">
          <video
            id="video"
            width="750"
            height="400"
            autoPlay
            muted
            controls
            loop
          >
            <source type="video/mp4" />
          </video>
        </div>

        <div className="content">{content}</div>
        <div>
          <button
            onClick={(e) => {
              setState(!state);
            }}
            className="button"
          >
            Next
          </button>
        </div>
      </div>
    </div>
  );
}

export default Tutorial;
