export const initialState = {
    user: null,
    win:0,
    played:0,
    lost:0,
    win_perc:0,
  };
  const reducer = (state, action) => {
    console.log(action);
    switch (action.type) {
        case "SET_USER":
            console.log("here",action.user)
            return {
              ...state,
              user: action.user,
              win:action.win,
              lost:action.lost,
              played:action.played,
              win_perc:action.win_perc,
            };
            case "SET_DATA":
            console.log("here",action.user)
            return {
              ...state,
              win:action.win,
              lost:action.lost,
              played:action.played,
              win_perc:action.win_perc,
            };
      default:
        return { ...state };
    }
  };
  export default reducer;
  