import React,{useEffect,useState} from 'react'
import "./Room.css";
import {createSocket} from "../conection"
import Axios from "axios";
import socketIOClient from 'socket.io-client';
import Peer from 'peerjs';
import Game from "./Game"
import Lobby from "./Lobby"
import {useStateValue} from "../StateProvider";
function Room() {
    // const [id,setID]=useState();
    const [otherID,setotherID]=useState("");
    const [color,setColor]=useState("blue");
    const [socket,setSocket]=useState(null);
    const [count,setCount]=useState(0);
    const [{user},dispatch]=useStateValue();
useEffect(()=>{
    var peer = new Peer(user?.uid);
    // ////console.log(peer);
const socket = socketIOClient("https://the-l-game-1.herokuapp.com/");
setSocket(socket);
////console.log(socket);


socket.on('connect',()=>{
    ////console.log("socket connected");


    // peer.on('open',(id)=>{
        // setID(id)
        // ////console.log(id);
        const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    const roomID=urlParams.get("id");
        // ////console.log(window.location)
        const userData={
            userID:user?.uid,
            roomID:roomID,
        }
        ////console.log('peer established and joined room',userData);
        socket.emit('join-room',userData);
    
        socket.on('new-user-connect', (userData) => {
            ////console.log('New User Connected', userData);
        });
        socket.on('get-users',(users)=>{
            ////console.log(users,user?.uid)
            if(users[0]==user?.uid)
            {
            setColor("red")
            setotherID(users[1]);
            ////console.log('creating request')
                const conn=peer.connect(users[1])
            }
            else
            {
            setColor("blue")
            setotherID(users[0]);
            }
            
        })
    
    // })
})
peer.on('connection',(conn)=>{
    ////console.log("request recieved")
    setotherID(conn.peer);
})
socket.on('state-changed',(state)=>{
    setCount(state);
})
socket.on("user-disconnected",(userID)=>{
    ////console.log('user disconnected-- closing peers', userID);
})
socket.on('disconnect', () => {
    ////console.log('socket disconnected --');
    peer.destroy();
});
socket.on('error', (err) => {
    ////console.log('socket error --', err);
});


peer.on('error',(err)=>{
    ////console.log('peer connection error', err);
        peer.reconnect();
})

},[])

    return (
        (otherID) ? <Game color={color} socket={socket}/>:<Lobby/>
    )
}

export default Room
